<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      </div>
      <div id="loading-container" class="nv-row row hover vs-con-loading__container">
        <div class="nv-col md:w-1/2 w-full">
          <p class="info-row">
            <span>{{ $t('CHEQUE_NUMBER') }}:</span>
            <span>{{ cheque.chequeNumber | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ACCOUNT_NUMBER') }}:</span>
            <span>{{ cheque.accountNumber | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CHEQUE_TYPE') }}:</span>
            <span>{{ cheque.typeName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CHEQUE_STATUS') }}:</span>
            <span>{{ cheque.statusName | placeholder('-') }}</span>
          </p>
        </div>
        <div class="nv-col md:w-1/2 w-full">
          <p class="info-row">
            <span>{{ $t('PAYEE') }}:</span>
            <span>{{ cheque.payee | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('AMOUNT') }}:</span>
            <span :class="negativeNumberStyle(cheque.amount)">{{ cheque.amount | currency('') | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CHEQUE_DATE') }}:</span>
            <span>{{ cheque.chequeDate | formatUnixDate }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CLEARING_DATE') }}:</span>
            <span>{{ cheque.clearingDate | formatUnixDate }}</span>
          </p>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import ChequeService from '@/services/cheque/ChequeService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'

export default {
  name: 'issued-cheque-view',
  mixins: [AlertMixin, NumberStyleMixin, LoadingMixin],
  data: function () {
    return {
      cheque: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      ChequeService.issuedCheques({
        chequeNumber: this.$route.params.id,
        accountNumber: this.$route.params.accountNumber
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.cheque = response.data.chequeList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
