import Endpoint from '@/services/Endpoint'

export default {
  issuedCheques (payload) {
    return Endpoint().get('/s1/connect/cheque/issued', {
      params: payload
    })
  },
  postDatedCheques (payload) {
    return Endpoint().get('/s1/connect/cheque/postDated/pending', {
      params: payload
    })
  },
  chequeBookWorkflowInstances (payload) {
    return Endpoint().get('/s1/connect/cheque/book/workflow/instance', {
      params: payload
    })
  },
  createChequeBook (payload) {
    return Endpoint().post('/s1/connect/cheque/book', payload)
  }
}
